html {
  font-style: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  bottom: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

body {
  max-width: 100vw;
  min-height: 100vh;
  height: 100%;
  font-family: "Open Sans", sans-serif;
  position: relative;
}

.slider {
  position: relative;
  width: 100%;
  max-width: 48.4375rem;
  border-radius: 8px 8px 0px 0px;
}
.slider.slick-slider {
  margin-bottom: 0rem !important;
}
.slider.slider-for {
  padding-bottom: 0.3125rem;
  background-color: #000;
}
.slider.slider-for .top-carousel .img-big {
  border-radius: 8px 8px 0px 0px;
  width: 48.4375rem;
  height: 23.25rem;
}
.slider.slider-for .top-carousel .box {
  position: absolute;
  width: 100%;
  max-width: 48.4375rem;
  background: linear-gradient(358.21deg, #000000 1.7%, rgba(196, 196, 196, 0) 122.29%);
  height: 23.375rem;
  border-radius: 0.5rem 0.5rem 0rem 0rem;
  top: 0;
  bottom: 0;
}
.slider.slider-for .top-carousel .box__content {
  position: absolute;
  z-index: 9999;
  padding-left: 1.5625rem;
}
.slider.slider-for .top-carousel .box__content h1 {
  color: white;
  bottom: 0;
  padding-bottom: 0.625rem;
}
.slider.slider-for .top-carousel .box__content__detail {
  color: #fff;
  font-weight: 400;
  font-size: 0.75rem;
  padding-bottom: 1.125rem;
}
.slider.slider-for .top-carousel .box__content__detail span {
  background-color: #0077b6;
  padding: 0.125rem 0.3125rem;
  font-size: 0.875rem;
  border-radius: 0.1875rem;
  font-weight: 43.75rem;
}
.slider.slider-nav {
  cursor: pointer;
  width: 100%;
  padding: 0rem 0.5rem 1.125rem 0.5rem;
  border-radius: 0rem 0rem 0.5rem 0.5rem;
  max-width: 48.4375rem;
  background: #000;
}
.slider.slider-nav .slider__card {
  width: 100%;
  margin-right: 0.5rem;
  color: #fff;
}
.slider.slider-nav .slider__card img {
  width: 100%;
  max-width: 11.5rem;
  margin-bottom: 1.125rem;
}
.slider.slider-nav .slider__card h3 {
  font-size: 0.8125rem;
}

